<style lang="scss" scoped src="./ThreadsPage.scss">
</style>
<template src="./ThreadsPage.html">
  <div></div>
</template>

<script>
import Navbar from '../../components/navbar/Navbar';
import LoadingIndicator from '../../components/loading-indicator/LoadingIndicator';
import MessageService from '../../services/MessageService';
import Config from '../../config/Config';

export default {
  name: 'threads-page',
  components: {
    Navbar,
    LoadingIndicator,
  },
  data() {
    return {
      config: Config,
      isLoading: false,
      posts: [],
      found: 0,
    };
  },
  mounted() {
    this.isLoading = true;
    MessageService.getPosts().then((response) => {
      this.posts = response.posts;
      this.found = response.found;
      this.isLoading = false;
    });
  },
};
</script>



