<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped src="./LoginPage.scss">
</style>
<template src="./LoginPage.html">
  <div></div>
</template>

<script>
import Navbar from '../../components/navbar/Navbar';
import LoadingIndicator from '../../components/loading-indicator/LoadingIndicator';
import LoginService from '../../services/LoginService';
import Config from '../../config/Config';


export default {

  name: 'login-page',
  components: {
    Navbar,
    LoadingIndicator,
  },
  data() {
    return {
      config: Config,
      isLoading: false,
      passcode1: '',
      passcode2: '',
      passcode3: '',
      passcode4: '',
      passcode5: '',
      passcode6: '',
      submitData: '',
      alias: this.$route.params.aliasid,
      aliasInParam: !!(this.$route.params.aliasid),
    };
  },
  methods: {
    moveToNext(message) {
      document.getElementById(message).focus();
    },
    doLogin() {
      this.submitData = `${this.passcode1}${this.passcode2}${this.passcode3}${this.passcode4}${this.passcode5}${this.passcode6}`;
      LoginService.doLogin(this.alias, this.submitData).then((response) => {
        console.log(response);
      });
    },
    doAlias() {
      window.location.href = `#/l/@${this.alias}`;
      console.log(this.$route);
      this.$router.push({ name: 'loginalias', params: { aliasid: `@${this.alias}` } });
      this.aliasInParam = true;
      LoginService.getToken(this.$route.params.aliasid).then((response) => {
        console.log(response);
        this.isLoading = false;
      });
      // location.reload();
    },
    doToken() {
      LoginService.getToken(this.$route.params.aliasid).then((response) => {
        console.log(response);
        this.isLoading = false;
      });
    },
  },
  mounted() {
    this.isLoading = true;
    console.log(`alias in param ${this.aliasInParam}`);
    if (this.aliasInParam) {
      LoginService.getToken(this.$route.params.aliasid).then((response) => {
        console.log(response);
        this.isLoading = false;
      });
    }
  },
};
</script>



