import HomePage from './pages/home/HomePage';
import ProfilePage from './pages/profile/ProfilePage';
import MessagesPage from './pages/messages/MessagesPage';
import ThreadsPage from './pages/threads/ThreadsPage';
import LoginPage from './pages/login/LoginPage';
import ReplyLink from './pages/replylink/ReplyLink';

export default [
  { name: 'home', path: '/', component: HomePage },
  { name: 'profile', path: '/profile', component: ProfilePage },
  { name: 'messages', path: '/messages', component: MessagesPage },
  { name: 'threads', path: '/threads', component: ThreadsPage },
  { name: 'loginalias', path: '/l/:aliasid', component: LoginPage, props: true },
  { name: 'login', path: '/l', component: LoginPage, props: true },
  { name: 'replylink', path: '/r/:aliasid', component: ReplyLink, props: true },
];



