import axios from 'axios';
import Config from '../config/Config';

export default {
  getToken(alias) {
    console.log(alias);
    console.log(Config.LOGINURL);
    const loginURLwithAlias = `${Config.LOGINURL}${alias}`;
    console.log(loginURLwithAlias);
    return axios.get(loginURLwithAlias).then(response => response.data);
  },
  doLogin(alias, passcode) {
    return axios.post(Config.POSTLOGINURL, {
      alias,
      otpcode: passcode,
    }).then(response => response.data);
  },
};



