// need to disabled eslint since eslint doesn't recognize I'm using this var.
const provinceArr = [ // eslint-disable-line no-unused-vars
  { value: 'AB', text: 'Alberta' },
  { value: 'BC', text: 'British Columbia' },
  { value: 'MB', text: 'Manitoba' },
