  { value: 'QC', text: 'Quebec' },
  { value: 'SK', text: 'Saskatchewan' },
  { value: 'YT', text: 'Yukon' }];

function getOS() {
  const userAgent = window.navigator.userAgent;
  const platform = window.navigator.platform;
  const macosPlatforms = ['Macintosh', 'MacIntel', 'MacPPC', 'Mac68K'];
  const windowsPlatforms = ['Win32', 'Win64', 'Windows', 'WinCE'];
  const iosPlatforms = ['iPhone', 'iPad', 'iPod'];
  let os = null;

  if (macosPlatforms.indexOf(platform) !== -1) {
    os = 'Mac OS';
  } else if (iosPlatforms.indexOf(platform) !== -1) {
    os = 'iOS';
  } else if (windowsPlatforms.indexOf(platform) !== -1) {
    os = 'Windows';
  } else if (/Android/.test(userAgent)) {
    os = 'Android';
  } else if (!os && /Linux/.test(platform)) {
    os = 'Linux';
  }

  return os;
}

export default {
  name: 'home-page',
  components: {
    Navbar,
  },
  data() {
    return {
      states: statesArr,
      countries: countryArr,
      selectedState: '',
      selectedCountry: '',
      message: '',
      plate: '',
      stateProv: 'State',
    };
  },
  methods: {
    sendMessage() {
      const myOS = getOS();
      let paramter = '?';
      if (myOS === 'Android') paramter = '?';
      if (myOS === 'iOS') paramter = '&';
      let smsLink = `sms:+16283332650${paramter}body=@${this.selectedState}${this.plate} ${decodeURI(this.message)}`;
      if (myOS === 'Mac OS') {
        smsLink = `iMessage://+16283332650${paramter}body=@${this.selectedState}${this.plate} ${decodeURI(this.message)}`;
      }
      console.log(smsLink);
      // window.open(smsLink);
      window.location.href = smsLink;
    },
    updateStates() {
      if (this.selectedCountry === 'US') {
        this.stateProv = 'State';
        this.states = statesArr;
      }
      if (this.selectedCountry === 'CA') {
        this.stateProv = 'Province';
        this.states = provinceArr;
      }
    },
  },
  mounted() {
    LocationService.getIP().then((response) => {
      if (response) {
        this.selectedCountry = response.country.code;
        console.log(this.selectedCountry);
        if (this.selectedCountry === 'CA') {
          this.stateProv = 'Province';
          this.states = provinceArr;
        }
      }
    });
  },
};
</script>




